//
// Checkbox & Radio
// --------------------------------------------------

.checkbox,
.radio {
  margin-bottom: 12px;
  padding-left: 28px;
  position: relative;
  transition: color .25s linear;
  font-size: ceil((@font-size-base * 0.933)); // ~14px
  line-height: 1.5; // 21px;
  font-weight: normal;

  .icons {
    color: #999;
    display: block;
    height: 20px;
    top: 0;
    left: 0;
    position: absolute;
    width: 20px;
    text-align: center;
    line-height: 20px;
    font-size: 20px;
    cursor: pointer;

    .icon-checked {
      .opacity(0);
    }
  }
}

.checkbox,
.radio {
  .icon-checked,
  .icon-unchecked {
    display: inline-table;
    position: absolute;
    left: 0;
    top: 0;
    background-color: transparent;
    margin: 0;
    opacity: 1;
    filter: none;
    transition: color .25s linear;

    &:before {
      font-family: 'hoiioicons';
      speak: none;
      font-style: normal;
      font-weight: normal;
      font-variant: normal;
      text-transform: none;
      -webkit-font-smoothing: antialiased;
      -moz-osx-font-smoothing: grayscale;
    }
  }
}
.checkbox {
  .icon-checked:before {
    content: "\e802";
  }
  .icon-unchecked:before {
    content: "\e803";
  }
}
.radio {
  .icon-checked:before {
    content: "\e801";
  }
  .icon-unchecked:before {
    content: "\e800";
  }
}

.checkbox,
.radio {
  input[type="checkbox"].custom-checkbox,
  input[type="radio"].custom-radio {
    outline: none !important;
    opacity: 0;
    position: absolute;
    margin: 0;
    padding: 0;
    left: 0;
    top: 0;
    width: 20px;
    height: 20px;


    &:hover:not(.nohover):not(:disabled) + .icons {
      .icon-unchecked {
        .opacity(0);
      }
      .icon-checked {
        opacity: 1;
        filter: none;
      }
    }

    &:checked + .icons {
      color: @brand-primary;

      .icon-unchecked {
        .opacity(0);
      }
      .icon-checked {
        opacity: 1;
        filter: none;
      }
    }

    &:disabled + .icons {
      cursor: default;
      color: #ddd;

      .icon-unchecked {
        opacity: 1;
        filter: none;
      }
      .icon-checked {
        .opacity(0);
      }
    }

    &:disabled:checked + .icons {
      color: #ddd;

      .icon-unchecked {
        .opacity(0);
      }
      .icon-checked {
        opacity: 1;
        filter: none;
        color: #ddd;
      }
    }

  }

  &.primary input[type="checkbox"].custom-checkbox,
  &.primary input[type="radio"].custom-radio {
    & + .icons {
      color: @brand-primary;
    }
    // Checked State
    &:checked + .icons {
      color: @brand-primary;
    }
    // Disabled state
    &:disabled + .icons {
      cursor: default;
      color: @brand-primary;

      &.checked {
        color: @brand-primary;
      }
    }

    &:indeterminate + .icons {
      color: @brand-primary;
    }

  }
  &.success input[type="checkbox"].custom-checkbox,
  &.success input[type="radio"].custom-radio {
    & + .icons {
      color: @brand-success;
    }
    // Checked State
    &:checked + .icons {
      color: @brand-success;
    }
    // Disabled state
    &:disabled + .icons {
      cursor: default;
      color: @brand-success;

      &.checked {
        color: @brand-success;
      }
    }
  }
  &.info input[type="checkbox"].custom-checkbox,
  &.info input[type="radio"].custom-radio {
    & + .icons {
      color: @brand-info;
    }
    // Checked State
    &:checked + .icons {
      color: @brand-info;
    }
    // Disabled state
    &:disabled + .icons {
      cursor: default;
      color: @brand-info;

      &.checked {
        color: @brand-info;
      }
    }

    &:indeterminate + .icons {
      color: @brand-info;
    }

  }
  &.warning input[type="checkbox"].custom-checkbox,
  &.warning input[type="radio"].custom-radio {
    & + .icons {
      color: @brand-warning;
    }
    // Checked State
    &:checked + .icons {
      color: @brand-warning;
    }
    // Disabled state
    &:disabled + .icons {
      cursor: default;
      color: @brand-warning;

      &.checked {
        color: @brand-warning;
      }
    }
  }
  &.danger input[type="checkbox"].custom-checkbox,
  &.danger input[type="radio"].custom-radio {
    & + .icons {
      color: @brand-danger;
    }
    // Checked State
    &:checked + .icons {
      color: @brand-danger;
    }
    // Disabled state
    &:disabled + .icons {
      cursor: default;
      color: @brand-danger;

      &.checked {
        color: @brand-danger;
      }
    }
  }
}


// Group Addon
.radio,
.checkbox {
  .input-group-addon & {
    margin: -2px 0;
    padding-left: 20px;

    .icons {
      color: mix(@gray-light, white, 38%);
    }
    input[type="checkbox"].custom-checkbox,
    input[type="radio"].custom-radio {
      &:checked + .icons {
        color: @gray;

        .icon-checked {
          color: @gray;
        }
      }
      &:disabled + .icons {
        color: fade(mix(@gray-light, white, 38%), 60%);
      }
      &:disabled:checked + .icons {
        color: fade(mix(@gray-light, white, 38%), 60%);

        .icon-checked {
          color: fade(mix(@gray-light, white, 38%), 60%);
        }
      }
    }
  }
}

.radio + .radio,
.checkbox + .checkbox {
  margin-top: 10px;
}

// Form inline style

.form-inline .checkbox, .form-inline .radio {
  padding-left: 32px;
}
