//
// Buttons
// --------------------------------------------------


// Base styles
// --------------------------------------------------

.btn {
  display: inline-block;
  margin-bottom: 0; // For input.btn
  letter-spacing: 0.3px;
  text-align: center;
  min-width: 86px;
  vertical-align: middle;
  touch-action: manipulation;
  cursor: pointer;
  background-image: none; // Reset unusual Firefox-on-Android default style; see https://github.com/necolas/normalize.css/issues/214
  border: 1px solid transparent;
  white-space: nowrap;
  .button-size(@padding-base-vertical; @padding-base-horizontal; @font-size-base; @line-height-base; @btn-border-radius-base; @btn-font-weight);
  .user-select(none);

  &,
  &:active,
  &.active {
    &:focus,
    &.focus {
      //.tab-focus();
        outline: none;
    }
  }

  &:hover,
  &:focus,
  &.focus {
    color: @btn-default-color;
    text-decoration: none;
  }

  &:active,
  &.active {
    outline: 0;
    background-image: none;
   // .box-shadow(inset 0 3px 5px rgba(0,0,0,.125));
  }

  &.disabled,
  &[disabled],
  fieldset[disabled] & {
    cursor: @cursor-disabled;
    .opacity(.65);
    .box-shadow(none);
  }

  a& {
    &.disabled,
    fieldset[disabled] & {
      pointer-events: none; // Future-proof disabling of clicks on `<a>` elements
    }
  }
}


// Alternate buttons
// --------------------------------------------------

.btn-default {
  .button-variant(@btn-default-color; @btn-default-bg; @btn-default-bg-hover; @btn-default-bg-focus; @btn-default-border);
}
.btn-primary {
  .button-variant(@btn-primary-color; @btn-primary-bg; @btn-primary-bg-hover; @btn-primary-bg-focus; @btn-primary-border);
}
// Success appears as green
.btn-success {
  .button-variant(@btn-success-color; @btn-success-bg; @btn-success-bg-hover; @btn-success-bg-focus; @btn-success-border);
}
// Info appears as blue-green
.btn-info {
  .button-variant(@btn-info-color; @btn-info-bg; @btn-info-bg-hover; @btn-info-bg-focus; @btn-info-border);
}
// Warning appears as orange
.btn-warning {
  .button-variant(@btn-warning-color; @btn-warning-bg; @btn-warning-bg-hover; @btn-warning-bg-focus; @btn-warning-border);
}
// Danger and error appear as red
.btn-danger {
  .button-variant(@btn-danger-color; @btn-danger-bg; @btn-danger-bg-hover; @btn-danger-bg-focus; @btn-danger-border);
}

.btn-upper{
    letter-spacing: 1px;
    text-transform: uppercase;
    font-size: 13px;
}



.btn-wide{
    min-width: @btn-wide-min-width;
    padding-right: @btn-wide-padding;
    padding-left: @btn-wide-padding;
    
}

.btn-inner-info{
    padding-left: 10px;
    font-size: 92%;
    font-weight: 300;
}


// Button outline
// -------------------------

.btn-outline-default{
    background-color: transparent;
    border-width:2px;
    font-weight: 700;
    .button-outline-variant(@btn-default-color);
}

.btn-outline-primary{
    background-color: transparent;
    border-width:2px;
    font-weight: 700;
    .button-outline-variant(@btn-primary-border);
}

.btn-outline-info{
    background-color: transparent;
    border-width:2px;
    font-weight: 700;
    .button-outline-variant(@btn-info-border);
}

.btn-outline-danger{
    background-color: transparent;
    border-width:2px;
    font-weight: 700;
    .button-outline-variant(@btn-danger-border);
}

.btn-outline-warning{
    background-color: transparent;
    border-width:2px;
    font-weight: 700;
    .button-outline-variant(@btn-warning-border);
}


// Link buttons
// -------------------------

// Make a button look and behave like a link
.btn-link {
  color: @link-color;
  font-weight: normal;
  border-radius: 0;

  &,
  &:active,
  &.active,
  &[disabled],
  fieldset[disabled] & {
    background-color: transparent;
    .box-shadow(none);
  }
  &,
  &:hover,
  &:focus,
  &:active {
    border-color: transparent;
  }
  &:hover,
  &:focus {
    color: @link-hover-color;
    text-decoration: @link-hover-decoration;
    background-color: transparent;
  }
  &[disabled],
  fieldset[disabled] & {
    &:hover,
    &:focus {
      color: @btn-link-disabled-color;
      text-decoration: none;
    }
  }
}


// Button Sizes
// --------------------------------------------------

.btn-lg {
  // line-height: ensure even-numbered height of button next to large input
  .button-size(@padding-large-vertical; @padding-large-horizontal; @font-size-large; @line-height-large; @btn-border-radius-large; @btn-font-weight);
}
.btn-sm {
  // line-height: ensure proper height of button next to small input
  .button-size(@padding-small-vertical; @padding-small-horizontal; @font-size-small; @line-height-small; @btn-border-radius-small; @btn-font-weight-small);
}
.btn-xs {
  .button-size(@padding-xs-vertical; @padding-xs-horizontal; @font-size-small; @line-height-small; @btn-border-radius-small; @btn-font-weight-small);
}

.btn-sm, .btn-xs{
    font-weight: normal;
    
}


// Block button
// --------------------------------------------------

.btn-block {
  display: block;
  width: 100%;
}

// Vertically space out multiple block buttons
.btn-block + .btn-block {
  margin-top: 5px;
}

// Specificity overrides
input[type="submit"],
input[type="reset"],
input[type="button"] {
  &.btn-block {
    width: 100%;
  }
}

// Button file
// --------------------------------------------------

.btn-file {
  position: relative;
  overflow: hidden;
}
.btn-file input[type=file] {
  position: absolute;
  top: 0;
  right: 0;
  min-width: 100%;
  min-height: 100%;
  font-size: 100px;
  text-align: right;
  filter: alpha(opacity=0);
  opacity: 0;
  background: red;
  cursor: inherit;
  display: block;
}
input[readonly] {
  background-color: white !important;
  cursor: text !important;
}

#img-preview{
    width: 100%;
    margin-bottom: 25px;
}
