//
// Signup / Signin
// -------------------------------------------------


.logo{
    text-align: center;
    margin-top: 60px;    
}   

.logo img{
    width: 30%;
    height: 30%;
}

#login-box {
    margin-bottom: 20px;
}

.panel-login{
    margin-top: 30px;
    border: none;
    box-shadow: none;
    background: none;
}

.panel-login >.panel-body{
    border: 1px solid #ddd; 
    border-radius: 10px;
    -webkit-box-shadow: 0 1px 1px rgba(0,0,0,.05);
    box-shadow: 0 1px 1px rgba(0,0,0,.05);
    background-color: #fff;
}

.panel-login .checkbox,
.panel-login .help-block {
    margin-bottom: 20px;
}   

.panel-login .panel-title{
    font-weight: 300;
    font-size: 40px;
    color: #fff; 
}   

.btn-group >.btn{
    font-weight: 500;   
}


.login-page,
.register-page,
.forgot-pass-page,
.tfa-page{
    background: -webkit-linear-gradient(bottom, #2c3e50 10%, #419ecd 90%);
    background:    -moz-linear-gradient(bottom, #2c3e50 10%, #419ecd 90%); 
    background:     -ms-linear-gradient(bottom, #2c3e50 10%, #419ecd 90%);
    background:      -o-linear-gradient(bottom, #2c3e50 10%, #419ecd 90%);
    min-height: 100%;
}

.login-page body,
.register-page body,
.forgot-pass-page body,
.tfa-page body{
    background:none;
}

.change-form{
    color: #fff;
    text-align: center
}



@media screen and (min-width: 992px) {

    .country,
    .first-name{
        padding-right: 8px;
    }
    .phone-number,
    .last-name{
        padding-left:8px;   
    }
}

@media screen and (max-width: @screen-sm-min) {

    .panel-login .panel-title{
        font-size:34px;
    }
    
    .panel-body {
        padding: @panel-body-sm-padding;
    }
    
    
}





