// Button variants
//
// Easily pump out default styles, as well as :hover, :focus, :active,
// and disabled options for all buttons

.button-color-border(@color){
    border-left: 1px solid darken(@color,10%);
}


.button-variant(@color; @background-normal; @background-hover; @background-focus; @border) {
  color: @color;
  background-color: @background-normal;
  border-color: @border;

  &:focus,
  &.focus {
    color: @color;
    background-color: @background-focus;
  }
  &:hover {
    color: @color;
    background-color: @background-hover;
  }
  &:active,
  &.active,
  .open > .dropdown-toggle& {
    color: @color;
    background-color: @background-focus;

    &:hover,
    &:focus,
    &.focus {
      color: @color;
      background-color: @background-focus;
    }
  }
  &:active,
  &.active,
  .open > .dropdown-toggle& {
    background-image: none;
  }
  &.disabled,
  &[disabled],
  fieldset[disabled] & {
    &,
    &:hover,
    &:focus,
    &.focus,
    &:active,
    &.active {
      background-color: @background-normal;
    }
  }

  .badge {
    color: @background-normal;
    background-color: @color;
  }
}

.button-outline-variant(@color) {
  color: @color;
  border-color: @color;

  &:focus,
  &.focus {
    color: #fff;
    background-color: @color;
  }
  &:hover {
    color: #fff;
    background-color: @color;
  }
  &:active,
  &.active,
  .open > .dropdown-toggle& {
    color: #fff;
    background-color: @color;

    &:hover,
    &:focus,
    &.focus {
      color: #fff;
    background-color: @color;
    }
  }
  &:active,
  &.active,
  .open > .dropdown-toggle& {
    background-image: none;
  }
  &.disabled,
  &[disabled],
  fieldset[disabled] & {
    &,
    &:hover,
    &:focus,
    &.focus,
    &:active,
    &.active {
      background-color: @color;
    }
  }

  .badge {
    color: @color;
    background-color: @color;
  }
}

// Button sizes
.button-size(@padding-vertical; @padding-horizontal; @font-size; @line-height; @border-radius; @font-weight) {
  padding: @padding-vertical @padding-horizontal;
  font-size: @font-size;
  line-height: @line-height;
  border-radius: @border-radius;
  font-weight: @font-weight;
}
